import MyList from '@/components/my-list/my-list.vue'
export default {
	components: {
		MyList,
	},
	data() {
		return {
			nodes: '',
			articleVo:{},
			collectsList:[],
			classifyList: [], // 分组数组
			attentionOrganize: {}, // 分组信息
			classifyIndex: 0,
			id:'4028d856788109dc0178811031bc000b',
			memberId: '',
			code:'',
			organizeId: '',//分组id
		};
	},
	onLoad(option){
		if(option.id){
			this.id = option.id
		}
		this.memberId = uni.getStorageSync('userInfo') ? JSON.parse(uni.getStorageSync('userInfo')).id : ''
		this.getAttentionOrganize()
	},
	methods:{
		// 获取文章细节
		getArticleDetail(){
			this.$Request.get(`/appArticleController.do?getArticleDetail&memberId=${this.memberId}&id=${this.id}`)
			.then(res => {
				this.code = res.code
				this.articleVo = res.data.articleVo
				this.articleVo.content = res.data.articleVo.content.replace(/\<img/gi, '<img style="max-width:100%;height:auto" ')
				this.attentionOrganize = res.data.attentionOrganize
				this.classifyIndex = this.classifyList && this.classifyList.findIndex(item => item.id == res.data.attentionOrganize.organizeId)
				this.organizeId = this.attentionOrganize.isAttention == 1 ? res.data.attentionOrganize.organizeId : ''
				if(res.code == 0){
					this.collectsList =  res.data.collectsList.map(item => {
						return {
							...item,
							studyDate: item.studyDate && item.studyDate.substring(0,10)
						}
					})
				}else if(res.code == '-118'){
					this.status = 'noMore'
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
		// 分组
		getAttentionOrganize(){
			this.$Request.get(`/appAttentionController.do?getAttentionOrganize&type=2&memberId=${this.memberId}`)
			.then(res => {
				if(res.code == 0){
					this.classifyList = res.data.organizeList
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
				this.getArticleDetail()
			})
		},
		bindClassifyChange(e){
			if(uni.getStorageSync('userInfo')){
				this.classifyIndex = e.detail.value
				this.organizeId = this.classifyList[e.detail.value].id
				this.$Request.get('/appAttentionController.do?changeAttentionOrganize',{
					memberId: this.memberId,
					id: this.id,
					type: 2,
					organizeId: this.organizeId
				}).then(res => {
					if(res.code == 0){
						this.getArticleDetail()
					}else{
						uni.showToast({
							title: res.info,
							icon: 'none'
						})
					}
				})
			}else{
				uni.showModal({
				    title: '提示',
				    content: '您尚未登录，是否去登录？',
				    success: function (res) {
				        if (res.confirm) {
				            uni.navigateTo({
				            	url:'/pages/loginAll/login'
				            })
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
			}
		},
		// 训练跳转详情
		gotoListDetail(item){
			uni.navigateTo({
				url: `/pages/train/imageMemory/numEleEntry?id=${item.id}`
			})
		},
		// 收藏训练
		clickAttention(item, index){
			if(uni.getStorageSync('userInfo')){
				let collectsId = item.id
				this.$Request.get(`/appAttentionController.do?takeCollectsAttention&memberId=${this.memberId}&collectsId=${collectsId}&organizeId=`)
				.then(res => {
					if(res.code == 0){
						this.collectsList[index].attentionNum = item.attentionType == 1 ? item.attentionNum - 1 : item.attentionNum + 1
						this.collectsList[index].attentionType = item.attentionType == 1 ? 0 : 1
					}else{
						uni.showToast({
							title: res.info,
							icon: 'none'
						})
					}
				})
			}else{
				uni.showModal({
				    title: '提示',
				    content: '您尚未登录，是否去登录？',
				    success: function (res) {
				        if (res.confirm) {
				            uni.navigateTo({
				            	url:'/pages/loginAll/login'
				            })
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
			}
		},
		// 点击收藏、取消收藏训练按钮
		clickButton(type){
			if(uni.getStorageSync('userInfo')){
				switch(type) {
				 case 'up':
					this.upArticleCollectsAttention()
					break;
				 case 'drop':
					this.dropArticleCollectsAttention()
					break;
				} 
			}else{
				uni.showModal({
				    title: '提示',
				    content: '您尚未登录，是否去登录？',
				    success: function (res) {
				        if (res.confirm) {
				            uni.navigateTo({
				            	url:'/pages/loginAll/login'
				            })
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
			}
		},
		upArticleCollectsAttention(){
			this.$Request.get(`/appAttentionController.do?upArticleCollectsAttention&memberId=${this.memberId}&articleId=${this.id}&organizeId=${this.organizeId}`)
			.then(res => {
				if(res.code == 0){
					uni.showToast({
						title: '收藏成功！',
						icon: 'none'
					})
					this.getArticleDetail()
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
		dropArticleCollectsAttention(){
			this.$Request.get(`/appAttentionController.do?dropArticleCollectsAttention&memberId=${this.memberId}&articleId=${this.id}`)
			.then(res => {
				if(res.code == 0){
					uni.showToast({
						title: '取消成功！',
						icon: 'none'
					})
					this.getArticleDetail()
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
		// 收藏文章
		clickArtAttention(){
			if(uni.getStorageSync('userInfo')){
				this.$Request.get(`/appAttentionController.do?takeArticleAttention&memberId=${this.memberId}&articleId=${this.id}&organizeId=${this.organizeId}`)
				.then(res => {
					if(res.code == 0){
						this.articleVo.attentionType = this.articleVo.attentionType == 1 ? 0 : 1
						this.articleVo.attentionNum = this.articleVo.attentionType == 1 ? this.articleVo.attentionNum + 1 : this.articleVo.attentionNum - 1
					}else{
						uni.showToast({
							title: res.info,
							icon: 'none'
						})
					}
				})
			}else{
				uni.showModal({
				    title: '提示',
				    content: '您尚未登录，是否去登录？',
				    success: function (res) {
				        if (res.confirm) {
				            uni.navigateTo({
				            	url:'/pages/loginAll/login'
				            })
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
			}
		},
		// 点击有用、无用
		clickUsefulness(type){
			if(uni.getStorageSync('userInfo')){
				switch(type) {
				 case 'use':
					this.usefulArticleThumbs()
					break;
				 case 'unUse':
					this.uselessArticleThumbs()
					break;
				} 
			}else{
				uni.showModal({
				    title: '提示',
				    content: '您尚未登录，是否去登录？',
				    success: function (res) {
				        if (res.confirm) {
				            uni.navigateTo({
				            	url:'/pages/loginAll/login'
				            })
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
			}
		},
		usefulArticleThumbs(){
			this.$Request.get(`/appAttentionController.do?usefulArticleThumbs&memberId=${this.memberId}&articleId=${this.id}`)
			.then(res => {
				if(res.code == 0){
					uni.showToast({
						title: '操作成功！',
						icon: 'none'
					})
					this.getArticleDetail()
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
		uselessArticleThumbs(){
			this.$Request.get(`/appAttentionController.do?uselessArticleThumbs&memberId=${this.memberId}&articleId=${this.id}`)
			.then(res => {
				if(res.code == 0){
					uni.showToast({
						title: '操作成功！',
						icon: 'none'
					})
					this.getArticleDetail()
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
	}
}
